"use client";
import React, { useState } from "react";

export default function Navbar() {
  const [open, setOpen] = useState(false);
  const [lang, setLang] = useState("en"); // 'en' or 'rw'

  const t = (en, rw) => (lang === "en" ? en : rw);

  return (
    <nav className="bg-white border-b sticky top-0 z-30">
      <div className="max-w-6xl mx-auto px-4">
        <div className="flex justify-between items-center h-16">
          <div className="flex items-center gap-3">
            <button className="text-green-700 font-extrabold text-lg">
              VerdIn
            </button>
            <span className="hidden sm:inline text-sm text-gray-600">
              {t("Input credit for farmers", "Inguzanyo y'ibikoresho")}
            </span>
          </div>

          <div className="hidden md:flex items-center gap-4">
            <NavLink label={t("Home", "Ahabanza")} />
            <NavLink label={t("Farmer", "Umuhinzi")} href="/farmer/dashboard" />
            <NavLink label={t("Admin", "Ubuyobozi")} href="/admin/dashboard" />
            <NavLink label="USSD" href="#ussd" />
            <LangSwitcher lang={lang} setLang={setLang} />
            <AuthButtons />
          </div>

          {/* Mobile menu button */}
          <div className="md:hidden flex items-center">
            <LangSwitcher lang={lang} setLang={setLang} compact />
            <button
              onClick={() => setOpen(!open)}
              aria-label="Toggle menu"
              className="p-2 rounded-md focus:outline-none focus:ring"
            >
              <svg
                className="w-6 h-6"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d={open ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16M4 18h16"}
                />
              </svg>
            </button>
          </div>
        </div>
      </div>

      {open && (
        <div className="md:hidden px-4 pb-4">
          <div className="flex flex-col gap-2">
            <MobileLink label={t("Home", "Ahabanza")} />
            <MobileLink
              label={t("Farmer Dashboard", "Dashboard y'Umuhinzi")}
              href="/farmer/dashboard"
            />
            <MobileLink
              label={t("Admin Dashboard", "Dashboard y'Ubuyobozi")}
              href="/admin/dashboard"
            />
            <MobileLink
              label={t("USSD Info", "Amakuru ya USSD")}
              href="#ussd"
            />
            <div className="pt-2 border-t mt-2">
              <AuthButtons stacked />
            </div>
          </div>
        </div>
      )}
    </nav>
  );
}

function NavLink({ label, href = "#" }) {
  return (
    <a
      href={href}
      className="text-gray-700 hover:text-green-700 px-2 py-1 rounded-md text-sm"
    >
      {label}
    </a>
  );
}

function MobileLink({ label, href = "#" }) {
  return (
    <a
      href={href}
      className="block text-gray-700 hover:bg-gray-100 px-3 py-2 rounded-md"
    >
      {label}
    </a>
  );
}

function LangSwitcher({ lang, setLang, compact = false }) {
  return (
    <div className={compact ? "ml-2" : "flex items-center gap-2"}>
      <button
        onClick={() => setLang("en")}
        className={`px-2 py-1 rounded ${
          lang === "en" ? "ring-1 ring-green-300" : "hover:bg-gray-100"
        }`}
      >
        EN
      </button>
      {!compact && (
        <button
          onClick={() => setLang("rw")}
          className={`px-2 py-1 rounded ${
            lang === "rw" ? "ring-1 ring-green-300" : "hover:bg-gray-100"
          }`}
        >
          RW
        </button>
      )}
    </div>
  );
}

function AuthButtons({ stacked = false }) {
  return (
    <div
      className={
        stacked ? "flex flex-col gap-2 mt-2" : "flex items-center gap-2"
      }
    >
      <a
        href="/auth/login"
        className="text-sm px-3 py-1 rounded-md border border-gray-200 hover:bg-gray-50"
      >
        Login
      </a>
      <a
        href="/auth/register"
        className="text-sm px-3 py-1 rounded-md bg-green-600 text-white hover:bg-green-700"
      >
        Register
      </a>
    </div>
  );
}
